/* CSS Document */
/* =Clearfix
--------------------------------------------------------------------------------------------------------*/
.clear {
  clear: both; }

.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden; }

/* ----- clear fix for floats ----- */
.clearfix {
  display: inline-block; }

* html .clearfix {
  height: 1%; }

/* hides clearfix from IE-mac */
.clearfix {
  display: block; }

/* end hide from IE-mac */
.alignleft {
  float: left; }

.alignright {
  float: right; }

.aligncenter {
  margin: auto;
  display: block; }

.alignnone {
  clear: both;
  font-size: 0;
  line-height: 0;
  margin: 0;
  padding: 0;
  border: 0;
  height: 0;
  width: 0; }

.centered {
  text-align: center; }

/* End Clearfix
--------------------------------------------------------------------------------------------------------*/
/* =Typography
--------------------------------------------------------------------------------------------------------*/
body {
